"use client";

import React, { useEffect, useRef, useState } from "react";
import Link from "next/link";

const HeroSection = () => {
  const marqueeRef = useRef<HTMLDivElement>(null);
  const navbarRef = useRef<HTMLDivElement>(null);
  const requestRef = useRef<number>(0);
  const position = useRef(0);
  const direction = useRef(1);
  const normalSpeed = 2;
  const speed = useRef(normalSpeed);
  const lastScrollY = useRef(0);
  const scrollTimeout = useRef<NodeJS.Timeout | null>(null);
  const [showBurger, setShowBurger] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);

  const animate = () => {
    if (marqueeRef.current) {
      const width = marqueeRef.current.scrollWidth / 2;
      position.current += speed.current * direction.current;

      if (position.current < -width) position.current += width;
      if (position.current > 0) position.current -= width;

      marqueeRef.current.style.transform = `translateX(${position.current}px)`;
    }
    requestRef.current = requestAnimationFrame(animate);
  };

  const handleScroll = () => {
    if (window.innerWidth < 768) {
      setShowBurger(true);
    } else {
      if (navbarRef.current) {
        const rect = navbarRef.current.getBoundingClientRect();
        setShowBurger(rect.bottom <= 0);
      }
    }

    const currentScrollY = window.scrollY;
    direction.current = currentScrollY > lastScrollY.current ? -1 : 1;

    speed.current = normalSpeed + 1.5;
    if (scrollTimeout.current) clearTimeout(scrollTimeout.current);
    scrollTimeout.current = setTimeout(() => {
      speed.current = normalSpeed;
    }, 150);

    lastScrollY.current = currentScrollY;
  };

  useEffect(() => {
    requestRef.current = requestAnimationFrame(animate);
    window.addEventListener("scroll", handleScroll, { passive: true });
    handleScroll();

    return () => {
      cancelAnimationFrame(requestRef.current);
      window.removeEventListener("scroll", handleScroll);
      if (scrollTimeout.current) clearTimeout(scrollTimeout.current);
    };
  }, []);

  return (
    <section className="h-screen w-full relative overflow-hidden bg-[#9a9a98] text-white flex flex-col justify-end">
      {/* Original Navbar */}
      <nav
        ref={navbarRef}
        className="absolute top-0 left-0 w-full z-20 bg-[#9a9a98] bg-opacity-90 backdrop-blur-md"
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-16 items-center">
            <div className="text-white text-2xl font-semibold">@ Anghelo</div>
            <div className="hidden md:flex space-x-8">
              <Link href="#about" className="text-white hover:text-gray-200 transition">About</Link>
              <Link href="#projects" className="text-white hover:text-gray-200 transition">Projects</Link>
              <Link href="#contact" className="text-white hover:text-gray-200 transition">Contact</Link>
            </div>
          </div>
        </div>
      </nav>

      {/* Sticky Burger Menu */}
      {showBurger && (
        <div
          className="fixed top-7 right-10 z-50 bg-black bg-opacity-70 p-4 rounded-full cursor-pointer flex flex-col items-center justify-center h-14 w-14"
          onClick={() => setMenuOpen(!menuOpen)}
        >
          <span className="block w-6 h-0.5 bg-[#9a9a98] mb-1"></span>
          <span className="block w-6 h-0.5 bg-[#9a9a98] mb-1"></span>
          <span className="block w-6 h-0.5 bg-[#9a9a98]"></span>
        </div>
      )}

 {/* Side Menu */}
{/* Right-side Menu */}
<div
  className={`fixed top-0 right-0 h-full w-64 bg-black z-50 transform transition-transform duration-300 ${
    menuOpen ? "translate-x-0" : "translate-x-full"
  } flex flex-col pt-20 px-6`}
>
  <Link
    href="#about"
    className="text-white text-lg py-3 border-b border-gray-700"
    onClick={() => setMenuOpen(false)}
  >
    About
  </Link>
  <Link
    href="#projects"
    className="text-white text-lg py-3 border-b border-gray-700"
    onClick={() => setMenuOpen(false)}
  >
    Projects
  </Link>
  <Link
    href="#contact"
    className="text-white text-lg py-3"
    onClick={() => setMenuOpen(false)}
  >
    Contact
  </Link>
</div>



      {/* Background image */}
      <img
        src="images/photo.png"
        alt="Background"
        className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 pointer-events-none h-full w-auto z-30"
      />

      {/* Marquee text */}
      <div
        ref={marqueeRef}
        className="absolute bottom-10 flex whitespace-nowrap text-[15vw] font-normal leading-none tracking-tight z-40 text-white"
      >
        <span className="mr-20"> — ANGHELO — DEARROZ</span>
        <span className="mr-20"> — ANGHELO — DEARROZ</span>
      </div>
    </section>
  );
};

export default HeroSection;
