"use client";

import Link from "next/link";

interface NavbarProps {
  menuOpen: boolean;
  setMenuOpen: React.Dispatch<React.SetStateAction<boolean>>;
}

const Navbar: React.FC<NavbarProps> = ({ menuOpen, setMenuOpen }) => {
  return (
    <>
      {/* Navbar */}
      <nav className="absolute top-0 left-0 w-full z-50 backdrop-blur-md">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-16 items-center">
            <div className="text-white text-2xl font-semibold">@ Anghelo</div>

            {/* Desktop links */}
            <div className="hidden md:flex space-x-8">
              <Link href="#about" className="text-white hover:text-gray-200 transition">
                About
              </Link>
              <Link href="#projects" className="text-white hover:text-gray-200 transition">
                Projects
              </Link>
              <Link href="#contact" className="text-white hover:text-gray-200 transition">
                Contact
              </Link>
            </div>

            {/* Mobile "Menu" text */}
            <div
              className="md:hidden text-white font-medium cursor-pointer"
              onClick={() => setMenuOpen(!menuOpen)}
            >
              Menu
            </div>
          </div>
        </div>
      </nav>

      {/* Side Menu */}
      <div
        className={`fixed top-0 right-0 h-full transform transition-transform duration-300 flex flex-col px-6 z-50
                    w-full md:w-1/3 ${menuOpen ? "translate-x-0" : "translate-x-full"}`}
        style={{ backgroundColor: "#333333" }}
      >
        {/* Close Button */}
        <div
          className="absolute top-5 right-5 z-50 bg-black bg-opacity-70 p-4 rounded-full cursor-pointer flex items-center justify-center h-14 w-14"
          onClick={() => setMenuOpen(false)}
        >
          <span className="absolute block w-6 h-0.5 bg-[#9a9a98] rotate-45"></span>
          <span className="absolute block w-6 h-0.5 bg-[#9a9a98] -rotate-45"></span>
        </div>

        <div className="mt-32 mb-4">
          <p className="text-gray-400 text-sm uppercase tracking-wider">Navigation</p>
          <hr className="border-gray-700 mt-1" />
        </div>

        <div className="flex flex-col space-y-8">
          <Link href="#home" className="text-white text-5xl font-normal py-2" onClick={() => setMenuOpen(false)}>
            Home
          </Link>
          <Link href="#about" className="text-white text-5xl font-normal py-2" onClick={() => setMenuOpen(false)}>
            About
          </Link>
          <Link href="#projects" className="text-white text-5xl font-normal py-2" onClick={() => setMenuOpen(false)}>
            Projects
          </Link>
          <Link href="#contact" className="text-white text-5xl font-normal py-2" onClick={() => setMenuOpen(false)}>
            Contact
          </Link>
        </div>
      </div>
    </>
  );
};

export default Navbar;
